import parseUrl from './parseUrl';
import { trim } from './shims';

/**
 * @todo add support for hash-router (compare by hash).
 */
export default function comparePaths(paths) {
  const currentUrl = parseUrl(window.location.href);

  return (
    paths.filter(path => {
      if (typeof path !== 'string') {
        console.warn('Path must be a string');
        return false;
      }

      return currentUrl && currentUrl.pathname === trim(path);
    }).length > 0
  );
}
