import comparePaths from '../utils/comparePaths';

window.__va__(app => {
  const [cfg] = app.config.files.filter(
    file => file.name === 'nodeObserver' && file.type === 'script'
  );

  if (!cfg || !cfg.options) {
    console.warn('nodeObserver config is empty or missed options');
    return;
  }

  const { message, selector, paths } = cfg.options;

  // If paths is specified -> run next code if paths is match.
  if (Array.isArray(paths) && paths.length > 0 && !comparePaths(paths)) {
    return;
  }

  let node = null;

  if (typeof selector === 'string') {
    node = document.querySelector(selector);
  }

  if (!node) {
    console.warn('Specified node is not found');
    return;
  }

  // Options for the observer (which mutations to observe)
  const config = { attributes: true, childList: true, subtree: true };

  // Callback function to execute when mutations are observed
  const callback = () => {
    if (typeof message === 'string') {
      app.emit('open');
      app.emit('textMessage', message);
    }
  };

  // Create an observer instance linked to the callback function
  const observer = new MutationObserver(callback);

  // Start observing the target node for configured mutations
  observer.observe(node, config);

  // Later, you can stop observing
  // observer.disconnect();
});
